body {
  background: #fff;
  font-family: "Roboto", sans-serif;
}

h2 {
  font-weight: 700;
  font-size: 36px;
  line-height: 1.2;
  color: #201b1b;
}

.input p {
  font-weight: 400;
  font-size: 14px;
  line-height: 1.3;
  color: #565656;
}
.input input {
  width: 100%;
  box-sizing: border-box;
  margin: 4px 0 0;
  border: 1px solid #d9d9d9;
  border-radius: 8px;
  padding: 14px 16px;
  background: #f2f2f2;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.3;
  color: #201b1b;
}
.input input::placeholder {
  color: #a6a6a6;
}
.input textarea {
  resize: none;
  height: 128px;
  width: 100%;
  box-sizing: border-box;
  margin: 4px 0 0;
  border: 1px solid #d9d9d9;
  border-radius: 8px;
  padding: 14px 16px;
  background: #f2f2f2;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.3;
  color: #201b1b;
}
.input textarea::placeholder {
  color: #a6a6a6;
}

.btn__yellow {
  padding: 12px 24px;
  border: none;
  font-weight: 600;
  font-size: 16px;
  line-height: 1.3;
  text-align: center;
  color: #201b1b;
  transition: all 0.3s;
  border-radius: 4px;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.12);
  background: #fac800;
}
.btn__yellow:hover {
  background: #ffde5c;
}
.btn__gray {
  border: none;
  padding: 12px 24px;
  font-weight: 600;
  font-size: 16px;
  line-height: 1.3;
  text-align: center;
  color: #201b1b;
  transition: all 0.3s;
  border-radius: 4px;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.12);
  background: #ededed;
}
.btn__gray:hover {
  background: #e3e3e3;
}
.btn__light {
  padding: 12px 24px;
  border: none;
  font-weight: 600;
  font-size: 16px;
  line-height: 1.3;
  text-align: center;
  color: #201b1b;
  transition: all 0.3s;
  border-radius: 4px;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.12);
  background: #fff6d1;
}
.btn__light:hover {
  background: #ffefaf;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 16px;
  box-sizing: content-box;
}

.nav {
  padding: 17px 0;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.nav__logo {
  width: 100%;
  max-width: 55px;
  display: block;
}
.nav__logo img {
  width: 100%;
}
.nav__phone {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 8px;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.3;
  color: #fff;
  transition: all 0.3s;
  margin: 0 16px 0 auto;
}
.nav__phone svg path {
  transition: all 0.3s;
}
.nav__phone:hover {
  color: #ffde5c;
}
.nav__phone:hover svg path {
  fill: #ffde5c;
}

.header {
  padding: 0 0 160px;
  background: url("../img/header_bg.jpg") right no-repeat;
  background-size: cover;
}
.header__info {
  max-width: 515px;
  margin: 83px 0 0;
}
.header h1 {
  font-weight: 600;
  font-size: 64px;
  line-height: 1;
  letter-spacing: -0.02em;
  color: #fff;
}
.header h1 span {
  color: #fac800;
}
.header__list {
  margin: 36px 0 0;
  display: grid;
  gap: 13px;
}
.header__list li {
  display: grid;
  grid-template-columns: 81px 1fr;
  gap: 20px;
  align-items: center;
}
.header__list p {
  font-weight: 700;
  font-size: 20px;
  line-height: 1.2;
  color: #fff;
}
.header__img {
  border-radius: 100%;
  width: 81px;
  height: 81px;
  border: 5px solid rgba(255, 255, 255, 0.32);
  position: relative;
}
.header__img img {
  width: 71px;
  height: 71px;
  object-fit: contain;
  position: relative;
}
.header__row {
  margin: 36px 0 0;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 10px;
}
.header__row button {
  width: 100%;
}

.advant {
  padding: 100px 0;
  position: relative;
  overflow: hidden;
}
.advant h2 {
  max-width: 500px;
}
.advant__title {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}
.advant__arrow {
  position: relative;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
}
.advant__arrow .swiper-button-prev,
.advant__arrow .swiper-button-next {
  position: relative;
  inset: auto;
  transform: none;
  margin: 0;
  width: 40px;
  height: 40px;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.12);
  background: #fac800;
  transition: all 0.3s;
  border-radius: 100%;
}
.advant__arrow .swiper-button-prev::after,
.advant__arrow .swiper-button-next::after {
  content: none;
}
.advant__arrow .swiper-button-prev svg,
.advant__arrow .swiper-button-next svg {
  width: 20px;
  height: 20px;
}
.advant__arrow .swiper-button-prev:hover,
.advant__arrow .swiper-button-next:hover {
  background: #ffde5c;
}
.advant__arrow .swiper-button-prev.swiper-button-disabled,
.advant__arrow .swiper-button-next.swiper-button-disabled {
  opacity: 0.6;
  background: #f2f2f2;
}
.advant__slider {
  margin: 40px 0 0;
  overflow: visible !important;
}
.advant .swiper-wrapper {
  overflow: visible;
}
.advant .swiper-pagination {
  display: none;
}
.advant__img {
  position: relative;
}
.advant__img img {
  width: 100%;
  height: 200px;
  object-fit: cover;
  border-radius: 12px;
}
.advant__img span {
  position: absolute;
  top: 12px;
  left: 12px;
  border-radius: 8px;
  background: #fff;
  padding: 10px;
  display: block;
}
.advant__img span svg {
  width: 20px;
  height: 20px;
}
.advant__slide h3 {
  font-weight: 700;
  font-size: 20px;
  line-height: 1.2;
  color: #201b1b;
  margin: 12px 0 0;
}
.advant__slide p {
  margin: 12px 0 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.3;
  color: #201b1b;
}

.stock {
  padding: 60px 0;
  background: #f2f2f2;
}
.stock__wrapper {
  margin: 40px 0 0;
  display: grid;
  grid-template-columns: 1fr;
  gap: 20px;
}
.stock__item {
  border-radius: 16px;
  padding: 24px 30px;
  box-shadow: 0 2px 15px -2px rgba(0, 0, 0, 0.12);
  background: #fff;
  display: grid;
  grid-template-columns: calc(50% - 15px) calc(50% - 15px);
  gap: 30px;
}
.stock__img {
  border-radius: 12px;
  width: 100%;
  overflow: hidden;
  position: relative;
}
.stock__img_slide {
  border-radius: 12px;
  overflow: hidden;
  position: relative;
}
.stock__img_slide img {
  width: 100%;
  min-height: 313px;
  object-fit: cover;
}
.stock__img_val {
  position: absolute;
  top: 16px;
  left: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 8px;
  padding: 4px 12px;
  border-radius: 99px;
  box-shadow: 0 2px 4px -2px rgba(0, 0, 0, 0.13);
  background: #fff;
  z-index: 5;
}
.stock__img_val p {
  font-weight: 400;
  font-size: 16px;
  line-height: 1.3;
  color: #201b1b;
}
.stock .swiper-button-prev,
.stock .swiper-button-next {
  inset: auto;
  margin: 0;
  width: 20px;
  height: 20px;
  transition: all 0.3s;
  top: 50%;
  left: 8px;
  transform: translateY(-50%);
}
.stock .swiper-button-prev::after,
.stock .swiper-button-next::after {
  content: none;
}
.stock .swiper-button-prev svg,
.stock .swiper-button-next svg {
  width: 20px;
  height: 20px;
}
.stock .swiper-button-prev svg path,
.stock .swiper-button-next svg path {
  transition: all 0.3s;
}
.stock .swiper-button-prev:hover path,
.stock .swiper-button-next:hover path {
  fill: #ffde5c;
}
.stock .swiper-button-next {
  left: auto;
  right: 8px;
}
.stock .swiper-pagination {
  position: absolute;
  transform: none;
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  inset: auto;
  bottom: 16px;
}
.stock .swiper-pagination span {
  border-radius: 2px;
  width: 30px;
  height: 5px;
  background: #fff;
  opacity: 0.5;
  margin: 0 !important;
  transition: all 0.3s;
}
.stock .swiper-pagination span.swiper-pagination-bullet-active {
  opacity: 1;
}
.stock__info h3 {
  font-weight: 700;
  font-size: 26px;
  line-height: 1.2;
  color: #201b1b;
}
.stock__info ul {
  margin: 16px 0 0;
  display: grid;
  gap: 4px;
}
.stock__info ul li {
  display: block;
}
.stock__info ul li p {
  display: inline;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.3;
  color: #565656;
}
.stock__info ul li p + p {
  margin: 0 0 0 8px;
  color: #201b1b;
}
.stock__info .btn__yellow {
  margin: 20px 0 0;
  width: 100%;
}
.stock__btn {
  margin: 8px 0 0;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 8px;
  width: 100%;
}

.import {
  padding: 100px 0;
  position: relative;
  overflow: hidden;
  z-index: 2;
}
.import .container {
  position: relative;
}
.import .container::before {
  content: "";
  display: block;
  width: 1180px;
  height: 491px;
  position: absolute;
  z-index: -1;
  right: -268px;
  top: 0;
  background: url("../img/map.png") center no-repeat;
  background-size: contain;
}
.import h2 {
  max-width: 550px;
}
.import__subtitle {
  margin: 50px 0 0;
  font-weight: 400;
  font-size: 20px;
  line-height: 1.3;
  letter-spacing: -0.01em;
  color: #201b1b;
  max-width: 800px;
}
.import__list {
  margin: 50px 0 0;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 50px;
}
.import__list li {
  display: grid;
  grid-template-columns: 40px 1fr;
  gap: 24px;
  align-items: start;
}
.import__list h3 {
  font-weight: 700;
  font-size: 20px;
  line-height: 1.2;
  color: #201b1b;
}
.import__list p {
  margin: 12px 0 0;
  font-weight: 400;
  font-size: 20px;
  line-height: 1.3;
  letter-spacing: -0.01em;
  color: #565656;
}
.import__num {
  width: 40px;
  height: 40px;
  border: 1px solid #fac800;
  border-radius: 4px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 700;
  font-size: 20px;
  line-height: 1.2;
  color: #fac800;
}

.banner {
  padding: 0 0 100px;
}
.banner__block {
  background: linear-gradient(133deg, #585858 0%, #313131 100%);
  border-radius: 20px;
  padding: 30px;
  position: relative;
  z-index: 5;
  overflow: hidden;
}
.banner__info {
  max-width: 500px;
}
.banner__info h2 {
  color: #fff;
}
.banner__info h2 span {
  color: #fac800;
}
.banner__info p {
  margin: 20px 0 0;
  font-weight: 400;
  font-size: 20px;
  line-height: 1.3;
  letter-spacing: -0.01em;
  color: #fff;
}
.banner__info .btn__yellow {
  margin: 40px 0 0;
  width: 100%;
  max-width: 250px;
}
.banner__bg {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  width: 666px;
  object-fit: cover;
  z-index: -1;
}

.equip {
  padding: 60px 0;
  background: #f2f2f2;
  position: relative;
  z-index: 2;
  overflow: hidden;
}
.equip h2 {
  max-width: 500px;
}
.equip::before {
  content: "";
  display: block;
  width: 1509px;
  top: 0;
  left: 0;
  bottom: 0;
  background: url("../img/equip_bg.svg") right no-repeat;
  background-size: cover;
  position: absolute;
  z-index: -1;
}
.equip .swiper-wrapper {
  overflow: visible;
}
.equip .swiper-pagination {
  display: none;
}
.equip__img {
  padding: 16px;
  border-radius: 20px;
  backdrop-filter: blur(8px);
  box-shadow: inset -9px 9px 9px 0 rgba(196, 196, 196, 0.3), inset -5px 5px 5px 0 rgba(255, 255, 255, 0.4), inset 9px -9px 9px 0 rgba(255, 255, 255, 0.4), inset 5px -5px 5px 0 rgba(196, 196, 196, 0.3), 0 4px 15px 0 rgba(0, 0, 0, 0.12);
  background: rgba(245, 245, 245, 0.25);
}
.equip__img img {
  width: 100%;
}
.equip__slider {
  margin: 40px 0 0;
  overflow: visible !important;
  position: relative;
}
.equip__slide {
  cursor: pointer;
}
.equip__slide p {
  margin: 16px 0 0;
  font-weight: 400;
  font-size: 20px;
  line-height: 1.3;
  letter-spacing: -0.01em;
  color: #201b1b;
}

.gallery {
  padding: 100px 0;
}
.gallery__subtitle {
  margin: 20px 0 0;
  font-weight: 400;
  font-size: 20px;
  line-height: 1.3;
  letter-spacing: -0.01em;
  color: #201b1b;
}
.gallery__wrapper {
  margin: 40px 0 0;
  display: grid;
  grid-template-columns: 61fr 57fr;
  gap: 20px;
}
.gallery__wrapper img {
  width: 100%;
  height: 100%;
  min-height: 100%;
  object-fit: cover;
  border-radius: 16px;
  display: block;
}
.gallery__row {
  display: grid;
  grid-template-columns: 736fr 444fr;
  gap: 20px;
  grid-column: 1/3;
}
.gallery__grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
}
.gallery__video {
  position: relative;
}
.gallery__video span {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 60px;
  height: 60px;
  border-radius: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #fac800;
  transition: all 0.3s;
}
.gallery__video:hover span {
  background: #ffde5c;
}

.equip__also {
  padding: 0 0 100px;
  background: #fff;
}
.equip__also::before {
  content: none;
}
.equip__also .equip__img {
  background: #f2f2f2;
  box-shadow: none;
}

.form {
  padding: 60px 0;
  background: url("../img/form_bg.jpg") center no-repeat;
  background-size: cover;
}
.form__wrapper {
  display: grid;
  grid-template-columns: 1fr 450px;
  gap: 20px;
}
.form__info {
  max-width: 600px;
  display: flex;
  flex-direction: column;
}
.form__subtitle {
  margin: 20px 0 auto;
  font-weight: 400;
  font-size: 20px;
  line-height: 1.3;
  letter-spacing: -0.01em;
  color: #201b1b;
}
.form__contact {
  margin: 20px 0 0;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 20px;
}
.form__contact a {
  display: grid;
  grid-template-columns: 26px 1fr;
  gap: 8px;
  align-items: center;
  font-weight: 700;
  font-size: 20px;
  line-height: 1.2;
  color: #201b1b;
  white-space: nowrap;
  transition: all 0.3s;
}
.form__contact a path {
  transition: all 0.3s;
}
.form__contact a:hover {
  color: #ffde5c;
}
.form__contact a:hover path {
  fill: #ffde5c;
}
.form__contact p {
  max-width: 200px;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.3;
  color: #201b1b;
}
.form__form {
  border-radius: 16px;
  padding: 24px;
  box-shadow: 0 2px 15px -2px rgba(0, 0, 0, 0.12);
  background: #fff;
}
.form__form h3 {
  font-weight: 700;
  font-size: 20px;
  line-height: 1.2;
  color: #201b1b;
}
.form__form .input {
  margin: 24px 0 0;
}
.form__form .btn__yellow {
  width: 100%;
  margin: 24px 0 0;
}

.footer {
  padding: 60px 0;
  background: url("../img/footer_bg.jpg") center no-repeat;
  background-size: cover;
}
.footer .container {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}
.footer__logo {
  display: block;
}
.footer__link {
  display: block;
  margin: 20px 0 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.3;
  text-decoration: underline;
  text-decoration-skip-ink: none;
  color: #fff;
  transition: all 0.3s;
}
.footer__link:hover {
  color: #ffde5c;
}
.footer__text {
  font-weight: 400;
  font-size: 16px;
  line-height: 1.3;
  color: #fff;
  max-width: 500px;
}
.footer__phone p {
  font-weight: 400;
  font-size: 14px;
  line-height: 1.3;
  color: #fff;
  text-align: right;
}
.footer__phone a {
  display: block;
  margin: 4px 0 0;
  font-weight: 700;
  font-size: 20px;
  line-height: 1.2;
  color: #fff;
  transition: all 0.3s;
  text-align: right;
}
.footer__phone a:hover {
  color: #ffde5c;
}
.footer .btn__yellow {
  margin: 20px 0 0;
}

@media (max-width: 1440px) {
  .header {
    padding: 0 0 80px;
    background-position: -200px 0;
  }
  .header__info {
    margin: 50px 0 0;
  }
  .advant,
  .import,
  .gallery {
    padding: 50px 0;
  }
  .banner,
  .equip__also {
    padding-bottom: 50px;
  }
}
@media (max-width: 1024px) {
  .stock__item {
    grid-template-columns: 1fr;
    gap: 12px;
  }
  .banner__bg {
    max-width: 420px;
    height: 100%;
    object-position: left;
  }
  .banner__info {
    max-width: 420px;
  }
  .form__wrapper {
    grid-template-columns: 1fr;
  }
  .footer .container {
    flex-direction: column;
    align-items: center;
    gap: 20px;
  }
  .footer__logo {
    width: max-content;
    margin: 0 auto;
  }
  .footer__text {
    text-align: center;
    max-width: 428px;
  }
  .footer__phone p,
  .footer__phone a {
    text-align: center;
  }
}
@media (max-width: 768px) {
  .nav__phone {
    gap: 4px;
    font-size: 14px;
  }
  .nav .btn__yellow {
    font-size: 14px;
    padding: 8px 12px;
  }
  h2 {
    font-size: 24px;
  }
  .header {
    padding: 0 0 40px;
    background: url("../img/header_bg_mob.jpg") center no-repeat;
    background-size: cover;
  }
  .header__info {
    margin: 44px 0 0;
    max-width: 100%;
  }
  .header h1 {
    font-size: 34px;
    max-width: 300px;
  }
  .header__list {
    margin: 20px 0 0;
    gap: 8px;
  }
  .header__list li {
    grid-template-columns: 58px 1fr;
    gap: 16px;
  }
  .header__list p {
    font-size: 18px;
  }
  .header__img {
    width: 58px;
    height: 58px;
    border-width: 4px;
  }
  .header__img img {
    width: 50px;
    height: 50px;
  }
  .header__row {
    margin: 220px 0 0;
    grid-template-columns: 1fr;
    gap: 10px;
  }
  .advant__arrow {
    display: none;
  }
  .advant__slider {
    margin: 20px 0 0;
  }
  .advant__slide h3 {
    font-size: 18px;
  }
  .advant__slide p {
    font-size: 14px;
  }
  .advant .swiper-pagination {
    position: relative;
    margin: 20px 0 0;
    inset: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    transform: none;
    gap: 8px;
  }
  .advant .swiper-pagination span {
    width: 12px;
    height: 12px;
    background: #333;
    transition: all 0.3s;
    margin: 0 !important;
  }
  .advant .swiper-pagination span.swiper-pagination-bullet-active {
    background: #fac800;
  }
  .stock {
    padding: 30px 0;
  }
  .stock__wrapper {
    margin: 20px 0 0;
    gap: 20px;
  }
  .stock__item {
    padding: 16px;
  }
  .import h2 {
    max-width: 368px;
  }
  .import__subtitle {
    margin: 20px 0 0;
    font-size: 16px;
  }
  .import__list {
    margin: 20px 0 0;
    grid-template-columns: 1fr;
    gap: 16px;
  }
  .import__list li {
