.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  overflow-y: auto;
  pointer-events: none;
  opacity: 0;
  z-index: 50;
  transition: all 0.3s;
  display: flex;
  padding: 15px;
}
.modal.active {
  opacity: 1;
  pointer-events: all;
}
.modal__overflow {
  position: fixed;
  z-index: -1;
  width: 100%;
  height: 100%;
  opacity: 0.5;
  background: #000;
  left: 0;
  top: 0;
}
.modal__block {
  margin: auto;
  width: 100%;
  max-width: 460px;
  border-radius: 12px;
  padding: 24px;
  box-shadow: 0 2px 15px -2px rgba(0, 0, 0, 0.12);
  background: #fff;
  position: relative;
}
.modal__close {
  position: absolute;
  top: 12px;
  right: 12px;
  cursor: pointer;
}
.modal h3 {
  font-weight: 700;
  font-size: 26px;
  line-height: 1.2;
  color: #201b1b;
}
.modal__subtitle {
  margin: 8px 0 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.3;
  color: #565656;
}
.modal__wrapper {
  margin: 16px 0 0;
  display: grid;
  grid-template-columns: 1fr;
  gap: 12px;
}
.modal .btn__yellow {
  margin: 12px 0 0;
  width: 100%;
}
.modal__link {
  margin: 16px 0 0;
  font-weight: 400;
  font-size: 12px;
  line-height: 1.3;
  text-align: center;
  color: #565656;
}
.modal__list {
  margin: 16px 0 0;
  display: grid;
  grid-template-columns: 1fr;
  gap: 8px;
}
.modal__list li {
  display: grid;
  grid-template-columns: auto 1fr;
  gap: 8px;
}
.modal__list p {
  font-weight: 400;
  font-size: 16px;
  line-height: 1.3;
  color: #201b1b;
}

@media (max-width: 600px) {
  .modal__block {
    padding: 16px;
  }
  .modal h3 {
    font-size: 20px;
  }
  .modal__subtitle {
    font-size: 14px;
  }
}